import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import VueLazyload from "vue-lazyload";
import Vant from "vant"; // 使用cdn导入时，要先全局导入vant
// import "./utils/Vant.js"; // 这是vant按需导入的代码，使用cdn导入后就可以注释掉了
// import "vant/lib/index.css"; // 开发环境时，这行代码需要有，才能导入vant样式，但是在生产环境时应该注释掉
// element/vant全局导入css样式的代码在打包时应该注释掉，因为生产环境会加载cdn，包含了css样式
// 但是在开发环境时要恢复这行代码，才能有UI组件的css样式（如果使用按需导入，则可以忽视这行注释，因为按需导入不需要全局导入样式）
// ----------------------------------------------
import "./styles/normalize.css";
import "./styles/global.css";
import "amfe-flexible";

Vue.use(Vant);
Vue.use(VueLazyload, {
  preLoad: 1.3, //预加载的宽高
  error: require("@/assets/img/error.png"),
  loading: require("@/assets/img/loading.png"),
  attempt: 1, //尝试加载的次数
});
Vue.config.productionTip = false;
new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
