import Vue from "vue";
import VueRouter from "vue-router";
// import store from "@/store/index.js";

Vue.use(VueRouter);

const routes = [
  {
    path: "/login",
    name: "Login",
    component: () => import("@/views/Login.vue"),
  },
  {
    path: "/",
    component: () => import("@/views/Layout.vue"),
    children: [
      {
        // 首页
        path: "", // 默认子路由
        name: "Home",
        component: () => import("@/components/Home.vue"),
      },
      {
        path: "/my",
        name: "My",
        component: () => import("@/components/My.vue"),
      },
      {
        name: "Search",
        path: "/search",
        component: () => import("@/components/search/Index.vue"),
      },
      {
        path: "/video",
        name: "Video",
        component: () => import("@/components/Video.vue"),
      },
    ],
  },
  {
    path: "*", // 在最下面添加一个path为 * 的路由路径，表示匹配所有路由路径，当上面的路由都没有匹配成功，则匹配此路由，用来设置404页面或使用路由重定向，跳转到指定页面
    redirect: "/", // 当路由路径匹配失败时，跳转到主页面
  },
];
const router = new VueRouter({
  // 在history模式下，正常的路由跳转也不会向后端发请求
  // 但是页面刷新或手动输入URL并按回车后(其实也相当于刷新)，这个路由跳转会作为一个get请求发送给后端，
  // 此时后端要是没有对这个请求处理，页面就会返回404
  mode: "history", // 开启history模式，开启后，上面的404页面匹配，在遇到多层子路由时就将失效
  // 需要在nginx中设置:当接收到未匹配成功的get请求时，重定向到主页面
  routes,
});

